<template>
    <div id="main">
        <!-- 顶部固定导航栏 -->
       <nHeader></nHeader>
       <!-- 这是子组件切换城市等 -->
       <div id="content">
            <div class="movie_menu">
                <router-link tag="div" to="/movie/city" class="city_name">
                    <span>{{$store.state.city.nm}}</span><i class="iconfont icon-lower-triangle"></i>
                </router-link>
                <div class="hot_swtich">
                    <router-link tag="div" to="/movie/nowplaying" class="hot_item">正在热映</router-link>
                    <router-link tag="div" to="/movie/comingSoon" class="hot_item">即将上映</router-link>
                </div>
                <router-link tag="div" to="/movie/search" class="search_entry">
                    <i class="iconfont icon-sousuo"></i>
                </router-link>
                
            </div>
            <!-- 这是二级路由的渲染 -->
            <keep-alive>
                <router-view />
            </keep-alive>
        </div>
        <nTabbar></nTabbar>
        <router-view name="detail"></router-view>
    </div>
</template>
<script>
// 引入公共组件
import nHeader from "@/components/Header/nheader.vue"
import nTabbar from "@/components/Tabbar/tabbar.vue"
import { MessageBox } from 'mint-ui';

export default {
    name: "movie",
    data: function(){
        return {
          
        }
    },
    components:{
        nHeader,
        nTabbar
    },
    mounted: function(){
        var _this = this;
        setTimeout(function(){
            _this.$http.get("/api/getLocation").then(function(res){
                // console.log(res)
                if(res.data.status === 0){
                    var id = res.data.data.id;
                    var nm = res.data.data.nm;
                    // 由于在本地存储取出的时候忘了进行数据的转换，所以这里不能用三等号，因为一个前者是字符串类型的，后者是一个整数类型的
                    if(_this.$store.state.city.id == id){
                        return;
                    }
                    
                    _this.messagebox = MessageBox.confirm("定位为"+nm+"，是否修改？").then(function(){
                        // 点击确定之后执行回调
                        _this.$store.state.city.nm = nm;
                        _this.$store.state.city.id = id;
                        // 将定位的数据存储到本地
                        window.localStorage.setItem("nowNM", nm);
                        window.localStorage.setItem("nowID", id);
                        // 修改完毕之后需要重新加载数据
                        window.location.reload();
                    })
                }
            })
        }, 2000)
    },
    
}
</script>
<style lang="scss" scoped>
    #content .movie_menu{ width: 100%; height: 45px; border-bottom:1px solid #e6e6e6; display: flex; justify-content:space-between; align-items:center; background:white; z-index:10;}
    .movie_menu .city_name{ margin-left: 20px; height:100%; line-height: 45px;}
    .movie_menu .city_name.active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
    .movie_menu .city_name.router-link-active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
    .movie_menu .hot_swtich{ display: flex; height:100%; line-height: 45px;}
    .movie_menu .hot_item{ font-size: 15px; color:#666; width:80px; text-align:center; margin:0 12px; font-weight:700;}
    .movie_menu .hot_item.active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
    .movie_menu .hot_item.router-link-active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
    .movie_menu .search_entry{ margin-right:20px; height:100%; line-height: 45px;}
    .movie_menu .search_entry.active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
    .movie_menu .search_entry.router-link-active{ color: #ef4238; border-bottom: 2px #ef4238 solid;}
    .movie_menu .search_entry i{  font-size:24px; color:red;}

    .slide-enter-active{ animation : 13s detailMove;}
    @keyframes detailMove{
        0%{
            transform : translateX(100%);
        }
        100%{
            transform : translateX(0);
        }
    }
</style>
